import React, { useState, useEffect } from "react";
import styled, { keyframes } from "styled-components";
import { TYPE } from "theme";
import axios from "axios";
import { FileUploader } from "react-drag-drop-files";
import { utils } from "ethers";
import NumericalInput from "components/NumericalInput";
import { AutoColumn } from "components/Column";
import TextareaAutosize from "react-textarea-autosize";
import { ButtonPrimary } from "components/Button";
import { useERCFactory } from "hooks/useContract";
import { calculateGasMargin } from "../../utils/calculateGasMargin";
import { useTransactionAdder } from "state/transactions/hooks";
import { useIsTransactionPending } from "state/transactions/hooks";
import { Redirect } from "react-router-dom";
import { useAllTransactions } from "state/transactions/hooks";
import { useActiveWeb3React } from "hooks/web3";
import ERCFACTORY_ABI from "abis/ercfactory.json";
import { useActiveListUrls, useAllLists } from "state/lists/hooks";
import uuid from "uuid";
const fileTypes = ["JPEG", "JPG", "PNG", "GIF"];

const StyledInput = styled.input`
  text-align: center;
  background-color: ${({ theme }) => theme.bg1};
  margin-right: 12px;
  font-size: 18px;
  margin: 0 auto;
  text-align: left;
  width: 100%;
  min-height: 40px;
  color: ${({ theme }) => theme.text2};
  border: 1px solid #2c2f36;
  font-weight: 500;
`;

const StyledNumInput = styled(NumericalInput)`
  text-align: center;
  background-color: ${({ theme }) => theme.bg1};
  margin-right: 12px;
  font-size: 18px;
  margin: 0 auto;
  text-align: left;
  width: 100%;
  min-height: 40px;
  color: ${({ theme }) => theme.text2};
  border: 1px solid #2c2f36;
  font-weight: 500;
`;

const InputTitle = styled(TYPE.mediumHeader)`
  color: ${({ theme }) => theme.text2};
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 10px;
`;

const TextArea = styled(TextareaAutosize)`
  color: ${({ theme }) => theme.text2};
  font-size: 18px;
  border: 1px solid #2c2f36;
  font-weight: 500;
  margin-bottom: 10px;
  min-height: 50px;
  background-color: ${({ theme }) => theme.bg1};
  border: 1px solid #2c2f36;
`;

const Start: React.FC = () => {
  const [text, setText] = useState("");
  const [description, setDescription] = useState("");
  const [telegram, setTelegram] = useState("");
  const [s, setS] = useState(0);
  const [x, setX] = useState("");
  const [website, setWebsite] = useState("");
  const [name, setName] = useState("");
  const transactionAdder = useTransactionAdder();
  const [image, setImage] = useState("");
  const [txHash, setTXHash] = useState(null);
  const pendingTX = useIsTransactionPending(txHash ?? undefined);
  const allTX = useAllTransactions();
  const { chainId, library } = useActiveWeb3React();
  const lists = useActiveListUrls();

  console.log(lists, "lists");

  const contract = useERCFactory();

  const [file, setFile] = useState(null);
  const handleChange = (file) => {
    const formData = new FormData();
    formData.append("image", file);

    try {
      // Send the file via Axios
      axios
        .post("/upload", formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          },
        })
        .then((result) => {
          setImage(result.data.url);
          setFile(file);
        });
    } catch (error) {
      console.error("Error uploading file:", error);
    }
  };

  useEffect(() => {}, [pendingTX]);

  if (txHash != null && pendingTX === false) {
    const receipt = allTX?.[txHash];

    // Create an Interface for decoding logs
    const contractInterface = new utils.Interface(ERCFACTORY_ABI);

    let tokenAddress = "";

    // Loop through logs in the receipt
    if (receipt && receipt.receipt && receipt.receipt.logs) {
      for (const log of receipt.receipt.logs) {
        try {
          // Decode the log using the contract's interface
          const parsedLog = contractInterface.parseLog(log);

          if (parsedLog.name == "TokenCreated") {
            tokenAddress = parsedLog.args[0];
          }

          // Check if the log matches the desired event (optional)
        } catch (error) {
          // Log decoding error (happens if log isn't from your contract)
          console.error("Error parsing log:", error);
        }
      }
      const n = uuid.v4();
      const numericPart = parseInt(n.replace(/-/g, "").slice(0, 15), 16);
      axios
        .post(
          "/update-json",
          {
            pathToUpdate: "tokens",
            newObject: {
              name: name,
              symbol: text,
              decimals: 18,
              address: tokenAddress,
              chainId: 181,
              logoURI: image,
            },
          },
          { headers: { "Content-Type": "application/json" } }
        )
        .then((res) => {
          axios.post(
            "/update-json",
            {
              pathToUpdate: "version.minor",
              newObject: numericPart,
            },
            { headers: { "Content-Type": "application/json" } }
          );
        });
      return <Redirect to={`/add/WATER/${tokenAddress}`} />;
    }
  }

  const handleSubmit = (event: React.FormEvent) => {
    event.preventDefault();

    if (!contract || !name || !text || s == "0") {
      return;
    }
    const wtx =
      website.slice(0, 512) +
      "|" +
      telegram.slice(0, 100) +
      "|" +
      x.slice(0, 100);

    contract.estimateGas
      .createToken(
        name,
        text.slice(0, 5).toUpperCase(),
        s,
        wtx,
        description,
        "https://hello.img"
      )
      .then((estimatedGasLimit) => {
        console.log("estimatedGasLimit", estimatedGasLimit);

        contract
          .createToken(
            name,
            text.slice(0, 5).toUpperCase(),
            s,
            wtx,
            description.slice(0, 512).toUpperCase(),
            "https://hello.img",
            { gasLimit: calculateGasMargin(estimatedGasLimit) }
          )
          .then((txid: any) => {
            console.log("txid", txid);

            transactionAdder(txid, { summary: "Token created: " + name });

            setTXHash(txid.hash);
          })
          .catch(console.error);
      })
      .catch(console.error);
  };

  return (
    <div style={{ padding: "18px" }}>
      <form onSubmit={handleSubmit}>
        <AutoColumn
          gap="6px"
          style={{ marginBottom: "12px", textAlign: "center" }}
        >
          <h2>Start a new coin</h2>
          <InputTitle fontSize={14} textAlign="center">
            Name
          </InputTitle>
          <StyledInput
            value={name}
            inputMode="text"
            disabled={false}
            onChange={(e) => {
              setName(e.target.value);
            }}
            placeholder={" "}
          />
          {/* <InputTitle fontSize={12} textAlign="center">
            new
          </InputTitle> */}

          <div style={{ marginBottom: "12px" }}>
            <InputTitle fontSize={14} textAlign="center">
              Ticker
            </InputTitle>
            <StyledInput
              inputMode="text"
              value={text}
              onChange={(e) => {
                setText(e.target.value.toUpperCase());
              }}
              placeholder={" "}
            />
          </div>
          <div style={{ marginBottom: "12px" }}>
            <InputTitle fontSize={14} textAlign="center">
              Total supply
            </InputTitle>
            <StyledNumInput
              inputMode="text"
              value={s}
              onUserInput={(text) => {
                setS(Number(text));
              }}
              placeholder={" "}
            />
          </div>
          <div style={{ marginTop: "12px" }}>
            <label>
              <InputTitle fontSize={14} textAlign="center">
                Description
              </InputTitle>{" "}
              <TextArea
                cols={34}
                value={description}
                rows={5}
                onChange={(e) => setDescription(e.target.value)}
              />
            </label>
          </div>
          <div className="App">
            <h4>Image</h4>
            <FileUploader
              multiple={false}
              handleChange={handleChange}
              name="file"
              types={fileTypes}
            />
            <p>{file ? `File name: ${file.name}` : "no files uploaded yet"}</p>
          </div>
          <div style={{ marginBottom: "12px" }}>
            <InputTitle fontSize={14} textAlign="center">
              Telegram
            </InputTitle>
            <StyledInput
              inputMode="text"
              value={telegram}
              onChange={(e) => {
                console.log(e, e.target);
                setTelegram(e.target.value);
              }}
              placeholder={" "}
            />
          </div>
          <div style={{ marginBottom: "12px" }}>
            <InputTitle fontSize={14} textAlign="center">
              Website
            </InputTitle>
            <StyledInput
              inputMode="text"
              value={website}
              onChange={(e) => setWebsite(e.target.value)}
              placeholder={" "}
            />
          </div>
          <div style={{ marginBottom: "12px" }}>
            <InputTitle fontSize={14} textAlign="center">
              X.com link
            </InputTitle>
            <StyledInput
              inputMode="text"
              value={x}
              onChange={(e) => setX(e.target.value)}
              placeholder={" "}
            />
          </div>
          <ButtonPrimary
            disabled={pendingTX || s === "0" || name === "" || text === ""}
          >
            Submit
          </ButtonPrimary>
        </AutoColumn>
      </form>
    </div>
  );
};

export default Start;
